<template>
<div>
 
  <!-- The Modal -->
  <div class="modal" id="myModal">
    <div class="modal-dialog">
      <div class="modal-content">

        <!-- Modal body -->
        <div class="modal-body">
         <form>
        <div class="form-group">
          <label for="update-title"> Title </label>
          <input v-model="title"  type="text" class="form-control" id="update-title" placeholder="">
        </div>
        <div class="form-group">
          <label for="update-note">Change the reminder</label>
          <textarea v-model="note" class="form-control" id="update-note"  rows="3"></textarea>
        </div>
        <button v-on:click="update(todoId)" class="btn btn-warning">Update</button>
      </form>
         
        </div>

        <!-- Modal footer -->
        <div class="modal-footer">
          <button @click="reloadPage" type="button" class="btn btn-danger" data-dismiss="modal">Close</button>
        </div>

      </div>
    </div>
  </div>
</div>
</template>
<script>
import TodoService from '../APIService'; // Import class TodoService 

export default {
  name: 'UpdateTodoModal',
  data(){
    return {
      title: "",
      note: ""
   
    
    }
  },
  props: ['todoId'], // Gets id from parent component
  methods: {
   async update(id) {
     await TodoService.updateTodo(this.title, this.note, id)
    },
    reloadPage(){
      window.location.reload(); // Reload page when click on close button
      },
    updateText(title, note) { // Get data from parent component
      this.title = title; 
      this.note = note;
    }
     
  }
}
</script>