import axios from 'axios';

// Url for proxy
const url = 'api/todolist/';

// Class with CRUD
class TodoService {

    // Get Todos
    static getTodos() {
        return new Promise(async (resolve, reject) =>{
            try {
                const res = await axios.get(url);
                const data = res.data;
                resolve (
                    data.map(todo => ({
                        ...todo,
                    }))
                )
            } catch(err) {
                reject(err);
            }
        })

    }

    // Create Todo
    static insertTodo(title, note) {
        return axios.post(url, {
            title: title,
            note: note
        });
    }

    // Delete todo
    static deleteTodo(id) {
        return axios.delete(`${url}${id}`);
    }

    // Update todo
    static updateTodo(title, note, id) {
        return axios.put(`${url}update/${id}`, {
            title: title,
            note: note
        });
    }
}

export default TodoService;