<template>
  <div class="wrapper">
    <div class="container">
      <h1> Virtual Post-it</h1>
      <h6>Never forget a thing!</h6>
      <div class="create-todo">
        <form>
          <div class="form-group">
            <label for="create-title">Create Title</label>
            <input v-model="title"  type="text" class="form-control" id="create-title" placeholder="Write the title on your post-it..">
          </div>
          <div class="form-group">
            <label for="create-note">Insert the reminder</label>
            <textarea v-model="note" class="form-control" id="create-note"  rows="3"></textarea>
          </div>
          <button v-on:click="createTodo" type="button" class="btn btn-success">Add Note</button>
        </form>
      </div>
      
      <p class="error" v-if="error"> {{ error }} </p>
    
      <div class="todo-container">
      
          <ul>
            <div class="todo" 
              v-for="(todo, index) in todos"
              v-bind:item="todo"
              v-bind:index="index"
              v-bind:key="todo._id"
              
              
            >
                <li>
                  <a>
                    
                    <button v-on:click="deleteTodo(todo._id)" type="button" class="btn btn-danger" id="delete">X</button>
                    <button v-on:click="updateThis(todo.todoTitle, todo.todoNote, todo._id)" class="btn btn-info" data-toggle="modal" id="modalbutton" data-target="#myModal"><i class="fas fa-edit"></i></button>
                  
                    <p class="title"> {{ todo.todoTitle }} </p>
                    <p class="note"> {{ todo.todoNote }} </p>

                  </a>
                </li>
            </div>
          </ul>
      </div>
      <UpdateTodoModal ref="UpdateModal" v-bind:todoId="currentId"/>
    </div>
  </div>
</template>

<script>
import TodoService from '../APIService'; // Import class TodoService
import UpdateTodoModal from './UpdateTodoModal'; // import child component

export default {
  name: 'TodoComponent',

  components: { UpdateTodoModal },
    data() {
      return {
        todos: [], 
        error: '',
        title: '',
        note: '',
        currentId: 0
      }
    },
    async created() {
      try {
        this.todos = await TodoService.getTodos(); // get all documents and push them to array 
      } catch(err) {
        this.error = err.message;
      }
    },
    methods: {
      async createTodo() {
        await TodoService.insertTodo(this.title, this.note);
        this.todos = await TodoService.getTodos();
      },
      async deleteTodo(id) {
        await TodoService.deleteTodo(id);
        this.todos = await TodoService.getTodos();
      },
      updateThis(title, note, id) {
        this.currentId = id;
        this.$refs.UpdateModal.updateText(title, note); // send data to child component
      },
    },
}


</script>

